* {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  /* border: red 1px solid; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.flex {
  display: flex;
}

.flex-center {
  justify-content: center;
}

.cell > img {
  width: 100%;
  height: 100%;
  border-radius: 6px;
}

.backdrop {
  border: none;
  /* background: #34495e; */
}

.backdrop > img {
  width: 100%;
  height: 100%;
}

.movie_header { 
  background: #34495e;
  color: #FFF;
  margin-top: -6px;
  padding: 1.5rem 0.5rem 0.6rem 0.5rem;
}

.movie_title {
  font-size: 1.3rem;
  padding: 10px 15px 5px 15px;
}

.movie_tagline {
  font-size: 0.9rem;
  padding: 2px 15px;
}

.movie_overview {
  line-height: 1.4rem;
  /* padding: 20px 15px; */
  padding: 1.5rem 1rem;
}

.movie_stats {
  background: #636e72;
  color: #FFF;
  padding: 1.5rem 1rem;
}

.movie_stats > li {
  list-style: none;
  padding: 4px 0;
}

.movie_stats > li:nth-child(2) {
  padding-bottom: 0px;
}

.chip {
  margin-left: 7px;
  margin-bottom: 3px;
}

.cast {
  padding: 1.5rem 1rem;
}

.cast__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cast__profile_pic {
  width: 3rem !important;
  height: 3rem !important;
  object-fit: cover;
  border-radius: 50%;
}

.cast__profile_pic--large {
  width: 8rem !important;
  height: 8rem !important;
}

/* .crew {
  padding: 15px;
} */

.ratings {
  background: #636e72;
  color: #FFF;
  display: flex;
  justify-content: center;
}

.critic {
  margin: 1.5rem 0.5rem;
}

.critic img {
  width: 3rem;
}

.critic p {
  font-size: 0.8rem;
  text-align: center;
  margin: 5px auto 0 auto;
}

.youtube {
  display: flex;
  overflow-x: scroll;
  width: 100%;
  /* height: 52.56%; */
}

.video {
  margin: 0.5rem 0.50rem;
}

.similar h2 {
  padding: 1.5rem 1rem 0 1rem;
}


/* Person component styling */

.person__hero {
  background: #34495e;
  color: #FFF;
  padding: 1.5rem 1rem;
  align-items: center;
  border: none;
}

.person__hero__details{
  margin-left: 1rem;
}

.person__biography {
  margin-top: -6px;
  padding: 1.5rem 1rem;
  background: #253546;
  color: #FFF;
  border: none;
}